*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

/* global styles */

.bold{
    font-weight: bold;
}

.grey{
    color: grey;
}

/* navbar */

nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    box-shadow: 0px 4px 9px 0px #c4c2c2;
}

.nav--logo{
    width: 60px;
}

.nav--ul{
    display: flex;
}

.nav--li{
    list-style: none;
    margin-inline: 0.5rem;
}

.nav--a{
    text-decoration: none;
    color: black;
    text-transform: uppercase;
}

/* gallery */

.gallery{
    padding: 5rem 2.5rem;
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    grid-auto-rows: 500px;
}


.card--img{
    object-fit: cover;
    height: 70%;
    width: 100%;
    border-radius: 10px;
}

.card--title{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}

.fa-star{
    font-size: 12px;
    padding-right: 5px;
}

.card--view{
    margin-bottom: 5px;
}

.card--availability{
    margin-bottom: 10px;
}

.price{
    margin-right: 5px;
}



